<template>
  <!-- TODO: Color styling og SVG's, set color on hover and active -->
  <div class="account-detail-info__container account-page">
    <LoadingWrapper :loadingKeys="[LOADING_KEY_FETCHING_ACCOUNT_INFO]">
      <BaseButton
        class="tertiary btn--px-20 account-detail-info__logout-btn"
        @click.native="logout"
        >{{ $t("logout") }}</BaseButton
      >
      <div class="account-page__title">
        <h2>{{ $t("personalDetail") }}</h2>
      </div>

      <form @submit.prevent="editCustomer">
        <div class="account-detail-info__body">
          <div class="row">
            <div class="col-xs-12">
              <div class="account-detail-info__label">
                {{ $t("firstName") }}
              </div>
              <div class="account-detail-info__input__container">
                <input
                  autofocus
                  type="text"
                  name="account.firstname"
                  id="account_firstname"
                  class="account-detail-info__input__text"
                  v-model="customerInfo.firstname"
                />
              </div>
              <p v-if="errorMsg.firstname" class="account-detail-info__error">
                {{ errorMsg.firstname }}
              </p>
            </div>
          </div>

          <div class="row row__container">
            <div class="col-xs-12">
              <div class="account-detail-info__label">{{ $t("lastName") }}</div>
              <div class="account-detail-info__input__container">
                <input
                  type="text"
                  name="account.lastname"
                  id="account_lastname"
                  class="account-detail-info__input__text"
                  v-model="customerInfo.lastname"
                />
              </div>
              <p v-if="errorMsg.lastname" class="account-detail-info__error">
                {{ errorMsg.lastname }}
              </p>
            </div>
          </div>

          <div class="row row__container">
            <div class="col-xs-12">
              <div class="account-detail-info__label">{{ $t("email") }}</div>
              <div class="account-detail-info__input__container">
                <input
                  type="text"
                  name="account.email"
                  id="account_email"
                  class="account-detail-info__input__text"
                  v-model="customerInfo.email"
                />
              </div>
              <p v-if="errorMsg.email" class="account-detail-info__error">
                {{ errorMsg.email }}
              </p>
            </div>
          </div>
        </div>

        <div class="row row__container">
          <div class="col-xs-12">
            <div class="account-detail-info__label">
              {{ $t("socialSecurityNumber") }}
            </div>
            <div class="account-detail-info__input__container">
              <input
                type="number"
                name="account.social-security-number"
                id="account_social-security-number"
                class="account-detail-info__input__text"
                v-model="customerInfo.nationid"
              />
            </div>
          </div>
        </div>

        <div class="account-detail-info__footer">
          <div class="account-detail-info__btn--save__wrap">
            <base-button
              type="submit"
              class="primary btn btn--rounded btn-block"
              >{{ $t("saveChange") }}</base-button
            >
          </div>
        </div>
      </form>
    </LoadingWrapper>
  </div>
</template>

<i18n>
{
  "is": {
    "personalDetail": "Um mig",
    "firstName": "Fornafn",
    "lastName": "Eftirnafn",
    "email": "Netfang",
    "socialSecurityNumber": "Kennitala (þarf eingöngu ef greiða á með millifærslu)",
    "saveChange": "Vista",
    "logout": "Skrá út"
  },
  "en": {
    "personalDetail": "Personal Details",
    "firstName": "First name",
    "lastName": "Last name",
    "email": "Email",
    "socialSecurityNumber": "Social security number (required only if transfer is required)",
    "saveChange": "Save changes",
    "logout": "Log out"
  }
}
</i18n>

<script>
import { mapGetters, mapActions } from "vuex";
import i18n from "@/i18n";

import BaseButton from "@/components/BaseButton";
import LoadingWrapper from "@/components/LoadingWrapper";
import { LOADING_KEY_FETCHING_ACCOUNT_INFO } from "@/store/loadingModule";

import { updateAccountInfo } from "@/services/customer";

import { isEmail } from "@/util/validators";
import get from "@/util/get";

export default {
  name: "AccountDetailInfo",
  components: {
    BaseButton,
    LoadingWrapper,
  },
  data() {
    return {
      errorMsg: {},
      LOADING_KEY_FETCHING_ACCOUNT_INFO,
    };
  },
  computed: {
    ...mapGetters(["customerInfo", "shippingAddress"]),
  },
  methods: {
    ...mapActions(["notification", "logout", "toggleLoading"]),
    validateCustomer(customer, errorMsg = {}) {
      const { firstname, lastname, email } = customer;
      errorMsg.firstname = i18n.t(!firstname ? "error.invalidTxt" : "");
      errorMsg.lastname = i18n.t(!lastname ? "error.invalidTxt" : "");
      errorMsg.email = i18n.t(
        !email ? "error.invalidTxt" : isEmail(email) ? "" : "error.invalidEmail"
      );

      this.errorMsg = errorMsg;
      if (errorMsg.firstname || errorMsg.lastname || errorMsg.email)
        return false;
      return true;
    },
    async editCustomer() {
      const {
        validateCustomer,
        customerInfo,
        shippingAddress,
        notification,
        toggleLoading,
      } = this;
      if (validateCustomer(customerInfo)) {
        const { firstname, lastname, email, nationid } = customerInfo;
        const params = {
          firstname,
          lastname,
          email,
          nationid,
          shipping_address_id: shippingAddress.customer_address_id,
          change_password: "0",
        };
        try {
          toggleLoading({
            loadingKey: LOADING_KEY_FETCHING_ACCOUNT_INFO,
            isLoading: true,
          });

          const data = await updateAccountInfo(params);

          if (data) {
            toggleLoading({
              loadingKey: LOADING_KEY_FETCHING_ACCOUNT_INFO,
              isLoading: false,
            });
            notification({
              type: data.status,
              message: get(data, "data.errors.0", data.message),
            });
          }
        } catch (error) {
          notification({
            type: "error",
            message: get(
              error,
              "response.data.messages.error.0.message",
              error.message
            ),
          });
        }
      }
    },
  },
};
</script>

