<template>
  <!-- TODO: Color styling og SVG's, set color on hover and active -->
  <div class="account-detail-password__container">
    <LoadingWrapper :loadingKeys="[LOADING_KEY_FETCHING_ACCOUNT_PASSWORD]">
      <div class="account-detail-password__header">
        <h2 class="account-detail-password__header__title">
          {{ $t("passwordUpdate") }}
        </h2>
      </div>

      <form @submit.prevent="changePassword">
        <div class="account-detail-password__body">
          <div class="row">
            <div class="col-xs-12">
              <div class="account-detail-password__label">
                {{ $t("currentPassword") }}
              </div>
              <div class="account-detail-password__input__container">
                <input
                  type="password"
                  name="account.password"
                  id="account_password"
                  class="account-detail-password__input__text"
                  autocomplete="current-password"
                  v-model="customer.password"
                />
              </div>
              <p
                v-if="errorMsg.password"
                class="account-detail-password__error"
              >
                {{ errorMsg.password }}
              </p>
            </div>
          </div>

          <div class="row row__container">
            <div class="col-xs-12">
              <div class="account-detail-password__label">
                {{ $t("newPassword") }}
              </div>
              <div class="account-detail-password__input__container">
                <input
                  type="password"
                  name="account.newPassword"
                  id="account_newPassword"
                  class="account-detail-password__input__text"
                  autocomplete="new-password"
                  v-model="customer.newPassword"
                />
              </div>
              <p
                v-if="errorMsg.newPassword"
                class="account-detail-password__error"
              >
                {{ errorMsg.newPassword }}
              </p>
            </div>
          </div>

          <div class="row row__container">
            <div class="col-xs-12">
              <div class="account-detail-password__label">
                {{ $t("confirmPassword") }}
              </div>
              <div class="account-detail-password__input__container">
                <input
                  type="password"
                  name="account.confirmPassword"
                  id="account_confirmPassword"
                  class="account-detail-password__input__text"
                  autocomplete="confirm-password"
                  v-model="customer.confirmPassword"
                />
              </div>
              <p
                v-if="errorMsg.confirmPassword"
                class="account-detail-password__error"
              >
                {{ errorMsg.confirmPassword }}
              </p>
            </div>
          </div>
        </div>

        <div class="account-detail-password__footer">
          <div class="account-detail-password__btn--save__wrap">
            <base-button
              type="submit"
              class="primary btn btn--rounded btn-block"
              >{{ $t("saveChange") }}</base-button
            >
          </div>
        </div>
      </form>
    </LoadingWrapper>
  </div>
</template>

<i18n>
{
  "is": {
    "passwordUpdate": "Breyta lykilorði",
    "currentPassword": "Núverandi lykilorð",
    "saveChange": "Vista"
  },
  "en": {
    "passwordUpdate": "Password Update",
    "currentPassword": "Current password",
    "saveChange": "Save changes"
  }
}
</i18n>

<script>
import { mapActions } from "vuex";
import i18n from "@/i18n";

import BaseButton from "@/components/BaseButton";
import LoadingWrapper from "@/components/LoadingWrapper";
import { LOADING_KEY_FETCHING_ACCOUNT_PASSWORD } from "@/store/loadingModule";

import { updateAccountInfo } from "@/services/customer";

import get from "@/util/get";

export default {
  name: "AccountDetailPassword",
  components: {
    BaseButton,
    LoadingWrapper,
  },
  data() {
    return {
      customer: {
        password: "",
        newPassword: "",
        confirmPassword: "",
      },
      errorMsg: {},
      LOADING_KEY_FETCHING_ACCOUNT_PASSWORD,
    };
  },
  methods: {
    ...mapActions(["notification", "toggleLoading"]),
    validatePassword(customer, errorMsg = {}) {
      const { password, newPassword, confirmPassword } = customer;

      errorMsg.password = i18n.t(!password ? "error.invalidTxt" : "");

      if (newPassword === "" && confirmPassword === "") {
        errorMsg.newPassword = i18n.t("error.invalidTxt");
        errorMsg.confirmPassword = i18n.t("error.invalidTxt");
      } else if (newPassword === "" && confirmPassword) {
        errorMsg.newPassword = i18n.t("error.invalidTxt");
      } else if (newPassword && confirmPassword === "") {
        errorMsg.confirmPassword = i18n.t("error.invalidTxt");
      } else if (newPassword !== confirmPassword) {
        errorMsg.confirmPassword = i18n.t("error.dulicatePassword");
      }

      this.errorMsg = errorMsg;
      if (errorMsg.password || errorMsg.newPassword || errorMsg.confirmPassword)
        return false;
      return true;
    },
    async changePassword() {
      const { validatePassword, customer, notification, toggleLoading } = this;
      if (validatePassword(customer)) {
        const { password, newPassword } = customer;
        const params = {
          change_password: "1",
          current_password: password,
          password: newPassword,
        };
        try {
          toggleLoading({
            loadingKey: LOADING_KEY_FETCHING_ACCOUNT_PASSWORD,
            isLoading: true,
          });

          const data = await updateAccountInfo(params);

          if (data) {
            toggleLoading({
              loadingKey: LOADING_KEY_FETCHING_ACCOUNT_PASSWORD,
              isLoading: false,
            });
            notification({
              type: data.status,
              message: get(data, "data.errors.0", data.message),
            });
          }
        } catch (error) {
          notification({
            type: "error",
            message: get(
              error,
              "response.data.messages.error.0.message",
              error.message
            ),
          });
        }
      }
    },
  },
};
</script>

